import Message from "../../UseCases/Contacts/Message";
import { PublicationModel } from "../Models/PublicationModel";

const base_api = 'https://pasdecarrierealaamondrans.go.yo.fr'
// const base_api = 'http://localhost'

export interface IAPIPublicationsRepository {
  listPublications(folderId: string): Promise<PublicationModel[]>,
  getPublications(fileId: string): Promise<PublicationModel>,
}

export interface IAPIContactsRepository {
  subscribe(emailAddress: string): Promise<any>
  contact(message: Message): Promise<any>
}

export class APIRepository implements IAPIPublicationsRepository, IAPIContactsRepository {
  async listPublications(folderId: string): Promise<PublicationModel[]> {
    const request = await fetch(`${base_api}/index.php?publications`)
    const response = await request
    return response.json()
  }

  async getPublications(fileId: string): Promise<PublicationModel> {
    const request = await fetch(`${base_api}/index.php?get&id=${fileId}`)
    const response = await request
    // console.log(response)
    return response.json()
    // return await request.json()
  }

  async subscribe(emailAddress: string) {
    const request = await fetch(`${base_api}/index.php?newsletter`, {
      method: 'POST',
      body: JSON.stringify({ contact: emailAddress })
    })
    return await request.json()
  }

  async contact(message: Message) {
    const request = await fetch(`${base_api}/index.php?contact`, {
      method: 'POST',
      body: JSON.stringify(message)
    })
    return await request.json()
  }
}
