import { useEffect, useState } from 'react';
import { Publication } from '../../../UseCases/Publications/Publication';
import { PublicationRepository } from '../../../UseCases/Publications/PublicationRepository';
import { APIRepository } from '../../../Repositories/DataSources/APIRepository';
import Markdown from '../Markdown/Markdown';
import { Box, CircularProgress } from '@mui/material';

export default function MarkdownPage({ pageId }: { pageId: string }) {
    const [loading, setLoading] = useState(true);
    const [publication, setPublication] = useState<Publication>()

    useEffect(() => {
        const publicationRepository = new PublicationRepository(new APIRepository())
        publicationRepository.getPublication(pageId)
            .then(pub => {
                setPublication(pub)
                setLoading(false)
            })
            .catch(console.log)
    }, [pageId])

    return (
        <Box className="publications">
            <Box className="publications-content">
                {
                    (loading)
                        ? <></>
                        : <Markdown input={publication?.body} />
                }
            </Box>
            <Box className="publications-loading">
                {loading ? <CircularProgress /> : <></>}
            </Box>
        </Box>
    )
}