import { Box, CircularProgress, List, ListItem } from "@mui/material";
import { useEffect, useState } from "react"

import './default.css';
import { APIRepository } from "../../Repositories/DataSources/APIRepository";
import { PublicationRepository } from "../../UseCases/Publications/PublicationRepository";
import { Publication } from "../../UseCases/Publications/Publication";

import Markdown from '../Components/Markdown/Markdown';
import { compareDates } from "../../UseCases/Tools/DateUtils";

export default function Publications() {
    const [loading, setLoading] = useState(true);
    const [publicationsList, setPublicationsList] = useState<Publication[]>([]);

    useEffect(() => {
        const publicationsRepository = new PublicationRepository(new APIRepository())
        publicationsRepository.listPublications()
            .then(data => {
                setPublicationsList(data.sort((a, b) => compareDates(a.date, b.date)).reverse())
                setLoading(false)
            })
            .catch(console.log)
    })

    return (
        <Box className="publications">
            <Box className="publications-content">
                <List>
                    {
                        (loading)
                            ? <></>
                            : publicationsList.map((publication, i) => (
                                <ListItem className="publication" key={i} disablePadding>
                                    <Markdown input={publication?.body} />
                                </ListItem>
                            ))
                    }
                </List>
            </Box>
            <Box className="publications-loading">
                {loading ? <CircularProgress /> : <></>}
            </Box>
        </Box>
    )
}