import { Box, Drawer, IconButton, Typography } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import FacebookIcon from '@mui/icons-material/Facebook';
import CloseIcon from '@mui/icons-material/Close';

import './NavBar.css';
import { useState } from 'react';
import { Link } from 'react-router-dom';

export type MenuItem = {
    name: string
    target: string
    disabled?: boolean
}

type Props = {
    menu: MenuItem[]
}

export default function NavBar({ menu }: Props) {
    const [open, setOpen] = useState(false);

    const toggleDrawer = (newOpen: boolean) => () => {
        setOpen(newOpen);
    };

    const DrawerMenu = (
        <Box className="drawer-menu" role="presentation" onClick={toggleDrawer(false)}>
            <Box className='drawer-menu-content'>
                <div className="close-button">
                    <IconButton aria-label='close navigation menu' onClick={toggleDrawer(false)}>
                        <CloseIcon fontSize='large' />
                    </IconButton>
                </div>
                <div className="menu-list">
                    {menu.map((page, i) => (
                        (
                            !page.disabled &&
                            <Link to={page.target} key={i}>
                                {page.name}
                            </Link>
                        )
                    ))}
                </div>
            </Box>
        </Box>
    );

    return (
        <Box className="header">
            <div className="app-menu">
                <div className="socials-links">
                    {/* <IconButton aria-label="facebook">
                        <FacebookIcon />
                    </IconButton> */}
                </div>
                <div className="menu-items">
                    <IconButton aria-label="menu"
                        onClick={toggleDrawer(true)}>
                        <MenuIcon />
                    </IconButton>
                    <Drawer
                        PaperProps={{
                            sx: { width: '100%', height: '100%', justifyContent: 'stretch' }
                        }}
                        open={open} onClose={toggleDrawer(false)}
                        anchor='right'>
                        {DrawerMenu}
                    </Drawer>
                </div>
            </div>

            <Box className="reason">
                <Typography>
                    Association de lutte contre le projet de carrire
                </Typography>
            </Box>
            <Box className="title">
                <Typography>Pas de carrire  La-Mondrans</Typography>
            </Box>
        </Box >
    )
}