import { IAPIPublicationsRepository } from "../../Repositories/DataSources/APIRepository"
import { Publication } from "./Publication"

export interface IPublicationRepository {
    listPublications(): Promise<Publication[]>
    getPublication(id: string): Promise<Publication>
}

export class PublicationRepository implements IPublicationRepository {
    private repository: IAPIPublicationsRepository

    constructor(repository: IAPIPublicationsRepository) {
        this.repository = repository
    }

    async getPublication(id: string): Promise<Publication> {
        const { createdTime, content } = await this.repository.getPublications(id)
        return { date: createdTime, body: content } as Publication
    }

    async listPublications(): Promise<Publication[]> {
        const listPubModel = await this.repository.listPublications('13sk_HLV8kZjXrJqx1FABHMB08s39SvLq')
        return listPubModel.map(pubModel => ({ date: pubModel.createdTime, body: pubModel.content } as Publication))
    }
}